.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #0b3d91, #1e90ff);
}

.login-card {
  background: #fff;
  padding: 40px 30px;
  border-radius: 16px;
  width: 350px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
  text-align: center;
  animation: fadeIn 1s ease;
}

.college-logo img {
  width: 70px;
  height: 70px;
}

.college-logo h2 {
  margin-top: 10px;
  color: #0b3d91;
  font-weight: 600;
  letter-spacing: 1px;
}

h3 {
  margin-top: 25px;
  color: #222;
}

.login-subtext {
  color: #666;
  font-size: 14px;
  margin-bottom: 25px;
}

.form-group {
  text-align: left;
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 6px;
  color: #444;
  font-weight: 500;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border: 1.5px solid #ccc;
  border-radius: 8px;
  font-size: 14px;
  transition: 0.3s;
}

.form-group input:focus {
  border-color: #1e90ff;
  outline: none;
  box-shadow: 0 0 4px rgba(30, 144, 255, 0.4);
}

.login-btn {
  background-color: #1e90ff;
  color: #fff;
  font-weight: 600;
  border: none;
  padding: 12px;
  border-radius: 8px;
  width: 100%;
  cursor: pointer;
  transition: background 0.3s ease;
}

.login-btn:hover {
  background-color: #0b3d91;
}

.signup-text {
  margin-top: 20px;
  font-size: 14px;
  color: #555;
}

.signup-text a {
  color: #1e90ff;
  text-decoration: none;
}

.signup-text a:hover {
  text-decoration: underline;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
