/* ===============================
   Unified 3D Admin Dashboard Theme
   =============================== */

:root {
  --bg-dark: #0f172a;
  --bg-darker: #1e293b;
  --text-light: #e2e8f0;
  --text-muted: #94a3b8;
  --accent-blue: #3b82f6;
  --accent-green: #22c55e;
  --accent-red: #ef4444;
  --shadow-dark: 8px 8px 20px #0a0e1a;
  --shadow-light: -8px -8px 20px #1e2a45;
}

/* ===============================
   Layout
   =============================== */
.admin-dashboard-container {
  display: flex;
  min-height: 100vh;
  background: radial-gradient(circle at top, var(--bg-dark), var(--bg-darker));
  color: var(--text-light);
  font-family: 'Poppins', sans-serif;
}

/* Sidebar fixed on the left */
.sidebar {
  width: 260px;
  background: linear-gradient(145deg, #1e293b, #0f172a);
  box-shadow: inset var(--shadow-dark), inset var(--shadow-light);
  display: flex;
  flex-direction: column;
  padding: 25px 20px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  overflow-y: auto;
  z-index: 100;
  border-right: 1px solid rgba(255, 255, 255, 0.05);
}

/* ===============================
   Sidebar Content
   =============================== */
.sidebar-userbox {
  background: linear-gradient(145deg, #1e293b, #0f172a);
  box-shadow: var(--shadow-dark), var(--shadow-light);
  border-radius: 14px;
  text-align: center;
  padding: 18px 10px;
  margin-bottom: 35px;
}

.sidebar-username {
  font-weight: 600;
  margin: 0;
  color: #e0eaff;
  font-size: 1.1rem;
}

.sidebar-role {
  color: var(--text-muted);
  font-size: 0.85rem;
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.sidebar-item {
  border: none;
  border-radius: 12px;
  padding: 14px 18px;
  font-size: 1rem;
  text-align: left;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  background: linear-gradient(145deg, #1e293b, #0f172a);
  color: var(--text-muted);
  box-shadow: var(--shadow-dark), var(--shadow-light);
  transition: all 0.25s ease-in-out;
}

.sidebar-item:hover {
  transform: translateY(-2px);
  color: var(--accent-blue);
}

.sidebar-item.active {
  background: linear-gradient(145deg, var(--accent-blue), #2563eb);
  color: #fff;
  box-shadow: 0 0 15px rgba(59, 130, 246, 0.6);
}

.sidebar-item .icon {
  font-size: 1.2rem;
}

/* Logout Section */
.sidebar-footer {
  margin-top: auto;
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.logout-btn {
  width: 100%;
  background: var(--accent-red);
  color: #fff;
  margin-bottom: 20px;
  padding: 10px 12px;
  border: none;
  border-radius: 10px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  cursor: pointer;
  transition: 0.2s;
  box-shadow: 0 0 10px rgba(239, 68, 68, 0.3);
}

.logout-btn:hover {
  background: #dc2626;
  box-shadow: 0 0 20px rgba(239, 68, 68, 0.6);
}

.logout-btn .icon {
  font-size: 16px;
}

/* ===============================
   Content Area
   =============================== */
.admin-content {
  margin-left: 260px;
  width: calc(100% - 260px);
  background: linear-gradient(145deg, var(--bg-darker), var(--bg-dark));
  padding: 40px;
  overflow-y: auto;
  box-shadow: var(--shadow-dark), var(--shadow-light);
  transition: transform 0.3s ease;
}
.admin-content:hover {
  transform: translateY(-4px);
}

/* ===============================
   Queue Page Styling
   =============================== */
.queue-page {
  text-align: center;
  color: var(--text-light);
  padding: 40px;
}

.queue-title {
  font-size: 2rem;
  color: var(--accent-blue);
  margin-bottom: 10px;
}

.subtitle {
  opacity: 0.8;
  margin-bottom: 30px;
  color: var(--text-muted);
}

.top-controls {
  margin-bottom: 30px;
}

.add-btn {
  background: linear-gradient(135deg, var(--accent-green), #16a34a);
  border: none;
  color: white;
  padding: 12px 25px;
  border-radius: 10px;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 0 10px var(--accent-green);
  transition: 0.3s;
}
.add-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px #22c55e;
}

.windows-grid {
  display: flex;
  justify-content: center;
  gap: 40px;
  flex-wrap: wrap;
}

.window-card {
  background: linear-gradient(145deg, var(--bg-darker), var(--bg-dark));
  border-radius: 20px;
  padding: 30px 20px;
  width: 260px;
  box-shadow: var(--shadow-dark), var(--shadow-light);
  transition: 0.4s ease;
  transform-style: preserve-3d;
}

.window-card:hover {
  transform: translateY(-10px) rotateY(6deg);
}

.window-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2rem;
  color: var(--accent-blue);
  margin-bottom: 10px;
}

.actions button {
  border: none;
  margin-left: 5px;
  cursor: pointer;
  border-radius: 6px;
  padding: 5px 8px;
  font-size: 0.8rem;
  color: white;
  transition: 0.3s;
}

.toggle-btn {
  background: var(--accent-blue);
}
.toggle-btn:hover {
  background: #2563eb;
}

.delete-btn {
  background: var(--accent-red);
}
.delete-btn:hover {
  background: #dc2626;
}

.ticket-number {
  font-size: 3rem;
  font-weight: 700;
  color: var(--accent-blue);
  text-shadow: 0 0 10px var(--accent-blue);
}

.student-name {
  margin-top: 10px;
  font-size: 1.1rem;
  color: var(--text-light);
}

.status-label {
  margin-top: 10px;
  font-size: 0.9rem;
  color: var(--text-muted);
}
.status-label span {
  color: var(--accent-green);
}

.window-card.closed .status-label span {
  color: #facc15;
}
.window-card.closed .ticket-number {
  color: #9ca3af;
  text-shadow: none;
}
.window-card.closed {
  opacity: 0.7;
  transform: scale(0.95);
}

/* Next Button */
.next-btn {
  background: linear-gradient(135deg, var(--accent-blue), #2563eb);
  color: #fff;
  border: none;
  border-radius: 12px;
  padding: 10px 20px;
  font-size: 1rem;
  margin-top: 20px;
  cursor: pointer;
  transition: 0.3s;
  box-shadow: 0 0 10px rgba(59, 130, 246, 0.5);
}
.next-btn:hover {
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.8);
}
.next-btn:disabled {
  background: #475569;
  cursor: not-allowed;
}

/* Waiting List */
.waiting-list {
  margin-top: 60px;
  background: rgba(30, 41, 59, 0.7);
  padding: 25px;
  border-radius: 16px;
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: var(--shadow-dark);
}

.waiting-list h3 {
  color: var(--accent-blue);
  margin-bottom: 15px;
}

.waiting-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.waiting-list li {
  background: rgba(15, 23, 42, 0.8);
  margin: 10px 0;
  padding: 12px;
  border-radius: 8px;
  font-size: 1rem;
  color: var(--text-light);
  transition: 0.3s;
}

.waiting-list li span {
  color: var(--accent-blue);
  font-weight: 600;
}

.waiting-list li:hover {
  transform: translateY(-4px);
  background: rgba(37, 99, 235, 0.15);
}

/* ===============================
   Responsive Sidebar
   =============================== */
@media (max-width: 900px) {
  .sidebar {
    position: relative;
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
    height: auto;
    border-radius: 0;
    box-shadow: none;
    background: var(--bg-darker);
  }

  .sidebar-nav {
    flex-direction: row;
    gap: 10px;
  }

  .sidebar-item {
    flex: 1;
    justify-content: center;
  }

  .sidebar-footer {
    display: none;
  }

  .admin-content {
    margin-left: 0;
    width: 100%;
  }
}
